'use client'

import Image from 'next/image'
import { useProjectPage } from '../hooks/useProjectPage';

export default function Project(){
    const { data, loading, error } = useProjectPage();

    // Convert Firebase object to array for easier mapping
    const projects = data ? Object.entries(data).map(([id, project]) => ({
        id,
        ...project
    })) : [];

    // Background colors for project cards
    const backgroundColors = [
        '#EEC052', // Yellow
        '#286F6E', // Teal  
        '#E74C3C', // Red
        '#9B59B6', // Purple
        '#3498DB', // Blue
        '#E67E22', // Orange
    ];

    // Default fallback images
    const fallbackImages = [
        '/assets/crmc.png',
        '/assets/nico.png'
    ];

    if (loading) {
        return (
            <div className="min-h-screen w-full py-16 md:py-24 lg:py-32 flex items-center justify-center">
                <div className="text-center">
                    <div className="animate-spin rounded-full h-32 w-32 border-b-2 border-[#286F6E] mx-auto"></div>
                    <p className="mt-4 text-lg text-gray-600">Loading projects...</p>
                </div>
            </div>
        );
    }

    if (error) {
        return (
            <div className="min-h-screen w-full py-16 md:py-24 lg:py-32 flex items-center justify-center">
                <div className="text-center text-red-500">
                    <p className="text-lg">Error loading projects: {error}</p>
                </div>
            </div>
        );
    }

    return(
        <div className="min-h-screen w-full py-16 md:py-24 lg:py-32">
            
            <div className="flex flex-col gap-6 md:flex-row md:justify-between md:items-center px-4 md:px-16 lg:px-32 xl:px-56">
            
            <div>
            <h1 className="text-[28px] md:text-[42px] lg:text-[50px] font-medium">My Latest Works</h1>
            <p className="text-[14px] md:text-[18px] lg:text-[20px] mt-2">Perfect solution for digital experience</p>
            </div>

            <p className="capitalize text-[#286F6E] font-semibold underline text-[14px] md:text-[16px]">Explore more works</p>

            </div>

            {/* Horizontally scrollable container */}
            <div className="mt-12 md:mt-16 lg:mt-20 overflow-x-auto scrollbar-hide pl-7 pr-16 md:pl-16 md:pr-16 lg:pl-24 lg:pr-32 xl:pr-56">
                <div className="flex gap-16 md:gap-8 lg:gap-14 pb-4" style={{ width: 'max-content' }}>

                    {projects.map((project, index) => {
                        const backgroundColor = backgroundColors[index % backgroundColors.length];
                        const fallbackImage = fallbackImages[index % fallbackImages.length];
                        
                        // Validate image URL - must be absolute URL or start with /
                        const isValidImageUrl = (url: string) => {
                            if (!url) return false;
                            return url.startsWith('/') || url.startsWith('http://') || url.startsWith('https://');
                        };
                        
                        const imageSource = isValidImageUrl(project.image_url) ? project.image_url : fallbackImage;
                        
                        return (
                            <div 
                                key={project.id}
                                className="h-[400px] w-[320px] md:h-[500px] md:w-[420px] lg:h-[536px] lg:w-[550px] rounded-[30px] md:rounded-[40px] lg:rounded-[50px] px-4 md:px-8 py-8 md:py-12 flex flex-col justify-between relative overflow-hidden flex-shrink-0"
                                style={{ backgroundColor }}
                            >
                                <div className="z-10">
                                    <h1 className="text-[20px] md:text-[28px] lg:text-[32px] font-semibold text-[#FFF5D8]">
                                        {project.type}
                                    </h1>
                                    <p className="text-[12px] md:text-[15px] lg:text-[16px] text-[#FFF5D8] mt-2">
                                        {project.title}
                                    </p>
                                </div>
                                <div className="absolute bottom-4 right-4 md:bottom-8 md:right-8 w-[180px] h-[120px] md:w-[260px] md:h-[160px] lg:w-[300px] lg:h-[200px] rounded-xl md:rounded-2xl overflow-hidden">
                                    <Image 
                                        src={imageSource} 
                                        alt={project.title} 
                                        width={300}
                                        height={200}
                                        className="object-cover w-full h-full"
                                        onError={() => console.log(`Failed to load image: ${project.image_url}, using fallback: ${fallbackImage}`)}
                                    />
                                </div>
                            </div>
                        );
                    })}

                </div>
            </div>
        </div>
    )
}